.basket_wrapper {
    width: 75%;
    margin: 0 auto;
}
/* Icon   =====================================  */
.basketIcon {
    display: block;
    position: fixed;
    top: 90%;
    right: 5%;
}
.basketIcon div,
.basketIcon_header div {
    position: relative;
}

.basketIcon_count {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 26px;
    height: 26px;
    top: -2px;
    right: 9px;
    color: #fff;
    border-radius: 50%;
    background-color: #0e9594;
}
.basketIcon img,
.basketIcon_header img {
    width: 73px;
    height: 72px;
}

/* Basket header ====================== */

.basket_header {
    display: flex;
    align-items: center;
    padding: 32px 54px 32px 28px;
}
.basket_header img {
    width: 26px;
    height: 26px;
}
.basket_header span {
    text-align: center;
    flex: 1;

    font-size: 25px;
    font-weight: bold;
}

/* Basket card ====================== */
.basket_card {
    width: 337px;
    height: 267px;
    box-sizing: border-box;
    padding: 12px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 10px;
}
.basket_card_product {
    display: flex;
}
.basket_card_product img {
    border-radius: 17px;
    width: 114px;
    height: 114px;
}
.basket_card_text {
    margin-left: 11px;
}
.basket_card_text div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
/* .basket_card_count {
} */
.basket_card_name {
    width: 90%;
    font-size: 20px;
    font-weight: bold;
}
.basket_card_description {
    font-size: 15px;
    color: #8f8f8f;
}

.basket_card_amount {
    padding: 2px 5px;
    width: fit-content;
    border-radius: 10px;
    color: #fff;
    background-color: #0e9594;
    margin-top: 7px;
}
.additions_title {
    margin-top: 7px;
    font-size: 15px;
    font-weight: bold;
}

.basket_header {
    display: flex;
    align-items: center;
    padding: 32px 28px;
}
.basket_header img {
    width: 26px;
    height: 26px;
}
.basket_header span {
    text-align: center;
    flex: 1;

    font-size: 25px;
    font-weight: bold;
}

/* Basket card ====================== */
.basket_card_container_mobile {
    margin: 0 auto;
}

.basket_card {
    width: 337px;
    height: fit-content;
    position: relative;
    box-sizing: border-box;
    padding: 12px;
    margin: 0 auto 12px;
    background-color: #fff;
    border-radius: 10px;
}
.basket_card_cancelIcon {
    position: absolute;
    height: 25px;
    width: 25px;
    right: 13px;
    height: 20px;
    width: 20px;
}
.basket_card_product {
    display: flex;
}
.basket_card_product img {
    border-radius: 17px;
    width: 114px;
    height: 114px;
}
.basket_card_text {
    margin-left: 11px;
}
.basket_card_name {
    font-size: 20px;
    font-weight: bold;
}
.basket_card_description {
    font-size: 15px;
    color: #8f8f8f;
}

.basket_card_amount {
    padding: 2px 10px;
    width: fit-content;
    border-radius: 10px;
    color: #fff;
    background-color: #0e9594;
    margin-top: 7px;
}
.additions_title {
    margin-top: 7px;
    font-size: 15px;
    font-weight: bold;
}
.basket_card_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
}
.basket_card_bottom button {
    width: fit-content;
    color: #fff;
    padding: 5px 13px;
    background-color: #696969;
    border: none;
    border-radius: 7px;
}
.basket_card_bottom span {
    font-size: 26px;
    color: #f2542d;
}

/* additions card ====================== */

.basket_card_additions_cards {
    display: flex;
    align-items: flex-end;
    overflow-x: scroll;
    gap: 32px;
    /* min-height: 76px; */
    max-width: 100%;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.additions_cards::-webkit-scrollbar {
    display: none;
}

.basket_card_additions_card {
    background-color: #fff;
    text-align: center;
    padding: 3px 3px 5px 3px;
    border-radius: 10px;
    width: 69px;
}

.basket_card_additions_card img {
    margin: 0 auto;
    width: 47px;
    height: 47px;
}
.basket_card_additions_card span {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 10px;
}
.basket_card_additions_card_title {
    margin-bottom: 10px;
}
/* bottom basket  ====================== */

.basket_bottom_mobile {
    box-sizing: border-box;
    position: fixed;
    width: 100%;
    height: fit-content;
    background-color: #fff;
    bottom: 0;
    padding: 15px;
}
.basket_bottom_mobile button {
    display: block;
    width: 90%;
    max-width: 300px;
    margin: 10px auto 0;
    padding: 12px 0;
    background-color: #f2542d;
    color: #fff;
    border: none;
    border-radius: 10px;
}

/* additions bottom ====================== */
.basket_adittions_title {
    margin-bottom: 7px;
    font-size: 15px;
    font-weight: bold;
}
.additions_cards {
    display: flex;
    align-items: flex-end;
    overflow-x: scroll;
    gap: 32px;
    max-width: 100%;
    height: 100px;
    padding-left: 7px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.additions_cards::-webkit-scrollbar {
    display: none;
}

.additions_card {
    position: relative;
    background-color: #fff;
    text-align: center;
    padding: 3px 3px 5px 3px;
    border-radius: 10px;
    width: 69px;
    height: 85px;
}
.additions_check_add,
.additions_check_delete {
    position: absolute;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    top: -7px;
    left: -6px;
}
.additions_check_add {
    background-color: #fff;
    color: rgba(14, 149, 148, 1);
}
.additions_check_delete {
    background-color: rgba(14, 149, 148, 1);
    color: #fff;
}
.additions_card img {
    margin: 0 auto;
    width: 47px;
    height: 47px;
}
.additions_card span:not(:first-of-type) {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 12px;
}
.additions_cards_title {
    margin-bottom: 10px;
}
.additions_card span:last-of-type {
    color: #f2542d;
}
.additions_card.active {
    background-color: #0e9594;
    color: #fff;
}
.additions_card.active span:last-of-type {
    color: #fff;
}

.basket_content {
    display: flex;
    justify-content: space-between;
}

/* additions dekstop  */
.basket_card_dekstop_additions {
    margin-top: 10px;
    display: flex;
}
.basket_card_dekstop_additions_title {
    width: 20px;
    height: 20px;
    text-align: center;
    border-radius: 50%;
    color: #fff;
    background-color: #0e9594;
    margin-right: 10px;
}

/* basket card dekstop  */
.basket_card_dekstop {
    width: fit-content;
    margin-top: 13px;
    padding-bottom: 23px;
    border-bottom: 1px solid #a5a8b0;
}
.basket_card_dekstop_product {
    display: flex;
}

.basket_card_dekstop_name,
.basket_card_dekstop_price {
    font-weight: bold;
}
.basket_card_dekstop_product_img {
    width: 75px;
    height: 75px;
    border-radius: 14px;
}
.basket_card_dekstop_name {
    margin-left: 12px;
    width: 125px;
}
.basket_card_dekstop_amount {
    width: 70px;
}
.basket_card_dekstop_count {
    width: 50px;
}
.basket_card_dekstop_price {
    width: 80px;
}
.basket_card_dekstop_change {
    width: 98px;
    height: fit-content;
    color: #fff;
    padding: 5px 13px;
    background-color: #f2542d;
    border: none;
    border-radius: 17px;
}
.basket_card_dekstop_cancelIcon {
    margin-left: 20px;
    width: 13px;
    height: 13px;
}
/* basketClientForm  */

.basketClientForm_wrapper {
    background-color: #fff;
    border-radius: 45px;
    padding: 30px;
}
.basketClientForm_title {
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 30px;
}
.basketClientForm_price_wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}
.basketClientForm_wrapper form {
    font-size: 22px;
}
.basketClientForm_price_wrapper span:last-of-type {
    font-size: 23px;
    font-weight: bold;
    color: #f2542d;
}
.basketClientForm_person {
    display: flex;
    flex-direction: column;
}
.basketClientForm_person span,
.basketClientForm_comment span {
    margin-bottom: 20px;
}
.basketClientForm_person input,
.basketClientForm_comment input {
    margin-bottom: 10px;
}
.basketClientForm_wrapper input,
.basketClientForm_wrapper textarea {
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    background-color: #d9d9d9;
    border: none;
    color: #fff;
    border-radius: 10px;
}
.basketClientForm_wrapper textarea {
    resize: none;
    width: 100%;
}
.basketClientForm_wrapper input::placeholder,
.basketClientForm_wrapper textarea::placeholder {
    color: #fff;
}
.basketClientForm_submit {
    margin-top: 10px;
    width: 100%;
    min-height: 66px;
    box-sizing: border-box;
    color: #fff;
    padding: 5px 13px;
    background-color: #f2542d;
    border: none;
    border-radius: 17px;
    cursor: pointer;
}

@media only screen and (max-width: 1300px) {
    .basket_wrapper {
        width: 95%;
    }
}
@media only screen and (max-width: 1024px) {
    .basket_content {
        display: block;
    }
    .basket_card_dekstop {
        margin: 0 auto;
    }
    .basketClientForm_wrapper {
        background: transparent;
        width: fit-content;
        margin: 0 auto;
    }
}
