.mainMenuClientDekstop_asideBasket {
    width: 433px;
    height: fit-content;
    padding: 0 32px;
    box-sizing: border-box;
    border-left: solid 1px #fff;
}
.mainMenuClientDekstop_asideBasket_title {
    font-size: 32px;
    font-weight: bold;
}
.mainMenuClientDekstop_asideBasket_price {
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    align-items: center;
    font-size: 32px;
    font-weight: bold;
}
.mainMenuClientDekstop_asideBasket_button {
    display: block;
    width: fit-content;
    color: #fff;
    padding: 15px 37px;
    border: none;
    background-color: #f2542d;
    border-radius: 46px;
    margin: 48px auto 0;
}
.mainMenuClientDekstop_asideBasket_cards {
    max-height: 420px;
    min-height: 100px;
    overflow-y: scroll;
}
/* card  */
.mainMenuClientDekstop_asideBasket_card {
    margin-top: 15px;
    display: flex;
    padding: 0 0 20px 0;
    border-bottom: solid 1px #fff;
}
.mainMenuClientDekstop_asideBasket_card_prodImg {
    width: 102px;
    height: 102px;
    border-radius: 17px;
}
.mainMenuClientDekstop_asideBasket_card_promo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding: 0 17px;
}
.mainMenuClientDekstop_asideBasket_card_promo_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mainMenuClientDekstop_asideBasket_card_promo_delete {
    width: 26px;
    height: 26px;
    cursor: pointer;
}
.mainMenuClientDekstop_asideBasket_card_promo_name {
    font-size: 18px;
    font-weight: bold;
}

.mainMenuClientDekstop_asideBasket_card_promo_bottom {
    text-align: right;
    font-size: 18px;
    font-weight: bold;
}
